body {
  background: -webkit-gradient(linear, left top, left bottom, from(#af67e9), to(#6565e7)) no-repeat;
  background: linear-gradient(#af67e9, #6565e7) no-repeat;
  height: 200vh;
  font-family: 'Kumbh Sans', sans-serif;
  font-size: 1.2rem;
  padding-top: 3rem;
}

p {
  color: #787887;
  font-size: 0.9rem;
  padding: 0;
  margin-block: 6px;
}

.faq {
  background-color: white;
  background-image: url(/images/bg-pattern-mobile.svg);
  background-size: 300px;
  background-repeat: no-repeat;
  margin-top: 7rem;
  background-position: 50% 1%;
  margin-inline: 2rem;
  border-radius: 25px;
}

@media (min-width: 1100px) {
  .faq {
    background-image: url(/images/bg-pattern-desktop.svg);
    background-position: -30%  -30%;
    background-size: 500px;
    margin-inline: 7rem;
    margin-top: 1rem;
  }
}

.faq__questions {
  padding: 1rem 1.5rem;
  position: relative;
  top: -5rem;
}

@media (min-width: 1100px) {
  .faq__questions {
    top: 0;
  }
}

.faq__box {
  display: none;
}

@media (min-width: 1100px) {
  .faq__box {
    display: block;
    position: absolute;
    left: -14.7rem;
    top: 23rem;
    -webkit-transform: scale(0.8);
            transform: scale(0.8);
    z-index: 3333;
  }
}

.faq__title {
  color: #4a4b5e;
  text-align: center;
  margin-bottom: 1.4rem;
}

.faq .questions {
  border-bottom: 1px solid #ccc;
  margin-bottom: 1.6rem;
}

.faq__heading {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
}

.faq__arrow {
  margin-top: 0.9rem;
  cursor: pointer;
  height: 7px;
}

.faq__ques-active {
  color: #4a4b5e;
  font-weight: bold;
}

.faq__answers {
  opacity: 0;
  max-height: 0;
  -webkit-transition: max-height 0.5s ease;
  transition: max-height 0.5s ease;
  margin-block: 10px;
}

@media (min-width: 1100px) {
  .faq .mobile {
    display: none;
  }
}

.faq__answers--active {
  opacity: 1;
  max-height: -webkit-fit-content;
  max-height: -moz-fit-content;
  max-height: fit-content;
}

.faq__images {
  position: relative;
  bottom: 8rem;
  width: 300px;
  height: auto;
  margin-inline: auto;
}

.hidden {
  display: none;
}

@media (min-width: 1100px) {
  .hidden {
    display: block;
    position: relative;
    left: -14rem;
    top: 14rem;
    -webkit-transform: scale(0.8);
            transform: scale(0.8);
  }
}
